.home-hero {
    height: 360px;
    padding: 50px;
}
.flex-home {
    display: flex;
    justify-content: space-around;
    padding-top: 60px;
    padding-left: 20px;
}
.home-hero-box {
    max-width: 700px;
    margin: auto;

}
.home-hero h2 {
    font-weight: lighter;
    width: 100%;
    text-align: center;
}
.designed-box:hover {
    animation-play-state: paused;
}
.designed-box {
    background-color: whitesmoke;
    width: 150px;
    height: 150px;
    border-radius: 25px;
    transform: rotateZ(40deg);
    animation-name: rotate-it;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: cubic-bezier(0.075, 0.82, 0.365, 1.5);
}
@keyframes rotate-it {
    0% {
        transform: rotateZ(45deg);
    }
    100% {
        transform: rotateZ(220deg);
    }
}
.home-hero .side-auth {
    margin: auto;
    margin-top: 20px;
    margin-top: 40px;
}

/*  -----------  media queries-----------      */
@media (max-width: 475px) {
    .home-hero {
        padding: 30px;
        width: 100%;
    }
    .home-hero h2 {
        width: 180px;
    }
}
@media (max-width: 395px) {
    .home-hero {
        height: 290px;
        padding: 30px;
    }
    .home-hero h2 {
        width: 100px;
    }
    .designed-box {
        border-radius: 15px;
        width: 120px;
        height: 120px;
    }
}
@media (max-width: 355px) {
    .designed-box {
        width: 100px;
        height: 100px;
    }
}
@media (max-width: 325px) {
    .designed-box {
        width: 80px;
        height: 80px;
    }
}
@media (max-width: 320px) {
    .home-hero {
        height: 330px;
    }
    .home-hero-box {
        flex-wrap: wrap;
        flex-direction: column-reverse;
    }
    .box {
        width: 100px;
        height: 80px;
        margin: auto;
    }
    .home-hero-box h2 {
        width: auto;
        margin: 0px auto  30px auto;

    }
    .side-auth {
        width: auto;
    }
}