#c-post {
    display: flex;
    margin-top: 30px;
    width: 100%;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;
    padding: 10px 10px;
}

/* Diplay//////////////////\/\\\// */
.dot-wrapper {
    display: flex;
    width: 60px;
    justify-content: space-around;
}
.dot {
    position: relative;
    width: 12px;
    height: 12px;
    background-color: blue;
    border-radius: 50%;
}
.dot:first-child {
    background-color: red;
}
.dot:last-child {
    background-color: green;
}
#post-content {
    font-size: 0.8rem;
}
#date {
    font-size: 0.8rem;
}
.display {
    background-color: white;
    color: black;
    margin-top: 20px;
    min-height: 420px;
    border-radius: 3px;
    width: 95%;
    padding: 10px;
    border: 1px solid rgb(199, 198, 198);
    word-break: break-all;
}
.display-wrapper {
    border-radius: 3px;
    width: 500px;
    display: block;
    background-color: black;
    position: relative;
    padding: 9px;
    margin: 0px auto;
}


/* input */
.post-input {
    width: 500px;
    position: relative;
    margin: 0px auto;
    background-color: var(--light-primary);
    padding: 10px;
    color: white;
    margin-bottom: 1px;
    border-radius: 5px;
    height: auto;
}
.grow-wrap {
    display: grid;
}
.grow-wrap::after {
    content: attr(data-replicated-value);
    white-space: pre-wrap;
    visibility: hidden;
}
.grow-wrap>textarea {
    resize: none;
    overflow: hidden;
}
.grow-wrap>textarea, .grow-wrap::after {
    border: 1px solid black;
    padding: 10px 20px;
    font: inherit;
    min-height: 325px;
    grid-area: 1 / 1 / 2/ 2;
    line-height: 26px;
    border-radius: 5px;
    min-width: 400px;
    box-sizing: border-box;
}
