.create-post-label {
    font-size: 1.3rem;
    text-transform: capitalize;
}
.create-post-input {
    border: none;
    width: 260px;
    height: 30px;
    margin: 5px 0px;
    border-radius: 3px;
    padding-left: 5px;
}
#tags-input {
    margin-left: 50px;
}
/* #add-tag {
    margin-left: 10px;
    background-color: white;
    color: black;
} */
.tag {
    margin-top: 5px;
    margin-left: 5px;
    background-color: var(--blue);
    color: white;
}
.tags-view {
    background-color: white;
    width: 100%;
    min-height: 40px;
    margin: 5px 0px;
    border-radius: 3px;
    padding: 5px;
    box-sizing: border-box;
}
.tags-view .tag:hover {
    color: rgb(99, 169, 248);
    background-color: rgb(21, 130, 255);
}
.tags-view .tag:hover::after {
    content: 'X';
    color: white;
    font-size: 1rem;
    font-weight: bold;
}
pre {
    line-height: 25px;
    font-size: 1rem;
    font-family: inherit;
    white-space: pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    white-space: -moz-pre-wrap;
    word-wrap: break-word;
}
#save {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 40px;
    height: auto;
    cursor: pointer;
}
#complete {
    width: 15px;
    height: 15px;
    margin-left: 5px;
}

#post {
    /* background-color: black;
    color: white; */
    width: 90px;
    height: 30px;
    font-weight: bold;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
    letter-spacing: 2px;
}