#search-panel {
    background-color: var(--primary);
    height: var(--header-height);
    width: 220px;
    position: fixed;
    top: 0px;
    left: 50%;
    margin-left: -110px;
    transition: top 700ms;
    box-sizing: border-box;
    z-index: 1;
}
.search-post {
    position: relative;
    width: 265px;
    height: 30px;
    margin: 10px auto;
}
.up-arrow {
    border:solid white;
    border-width: 0px 3px 3px 0px;
    display: block;
    width: 10px;
    height: 10px;
    margin: 12px auto;
    transform: rotate(-135deg);
}
div#close-search-panel {
    background-color: transparent;
    padding: 2px;
    margin-top: -20px;
    margin:-15px auto;
    display: none;
    width: 30px;
    height: 25px;
    cursor: pointer;
}

#search-input {
    height: 30px;
    box-sizing: border-box;
    border: none;
    width: 180px;
    border-radius: 50px 0px 0px 50px;
    padding-left: 8px;
}
#clear-search-btn {
    background-color: white;
    color: black;
    margin-left: -10px;
    padding-left: 8px;
    border-radius: 0%;
}
#clear-search-btn:hover {
    background-color: gray;
    color: white;
    transition: all 300ms;
}
#search-btn:hover {
    background-color: gray;
    color: white;
    transition: all 300ms;
}
#search-btn svg {
    fill: black;
    width: 13px;
}
#search-btn {
    margin-top: 3px;
    margin-left: -9px;
    width: 30px;
    padding-top: 4px;
    padding-left: 6px;
    border: none;
    background-color: white;
    color: black;
    border-radius: 0px 50px 50px 0px;
    height: 30px;
}
.close-btn {
    background-color: transparent;
    width: 20px;
    height: 20px;
    font-weight: bold;
    padding: 0px;
    transition: background-color 600ms;
    border-radius: 50%;
    position: absolute;
    font-size: 1rem;
    right: 5px;
    top: 5px;
}
.close-btn:hover {
    background-color: rgb(190, 190, 190);
}
#search-list {
    top: 55px;
    left: 5px;
    width: 220px;
    z-index: 2;
    position: absolute;
    display: block;
    min-height: 100px;
    font-size: 0.9rem;
    font-weight: 600;
    max-height: 250px;
    background-color: white;
    overflow-y: auto;
    padding: 10px;
    scrollbar-width: thin;
    box-sizing: border-box;
    border-radius: 2px;
    box-shadow: 0px 0px 4px rgb(80, 80, 80);
}
#search-list a {
    text-decoration: none;
    color: rgb(73, 73, 73);
}
#search-list a:hover {
    color: black;
}
#search-list::-webkit-scrollbar{
    width: 3px;
}
#search-list::-webkit-scrollbar-track {
    background-color: gray;
}
#search-list::-webkit-scrollbar-thumb {
    background-color: black;
}
#search-list::-webkit-scrollbar-corner {
    border-radius: 50%;
}
.title-search {
    padding: 5px 0px;
    word-wrap: break-word;
    border-bottom: 0.1px solid gray;
}
@media (max-width: 700px) {
    #search-panel {
        background-color: var(--primary);
        height: var(--header-height);
        width: 240px;
        position: fixed;
        top: 0px;
        left: 50%;
        margin-left: -120px;
        transition: top 400ms;
        box-sizing: border-box;
        z-index: 1;
    }
    div#close-search-panel {
        display: block;
    }
}