.auth-form {
    max-width: 360px;
    padding: 25px;
    border-radius: 10px;
    box-shadow: 2px 2px 10px gray;
    margin: 80px auto;
}
.auth-input {
    display: block;
    width: 350px;
    height: 40px;
    border: 1px solid black;
    border-radius: 8px;
    box-sizing: border-box;
    padding: 10px;
    margin-bottom: 30px;
    font-size: 1rem;
}
.auth-input:focus {
    border: 2px solid black;
}
.auth-label {
    display: block;
    font-weight: 500;
    font-size: large;
    margin-top: 30px;
}
.auth-submit {
    display: block;
    background-color: var(--primary);
    color: white;
    margin: 20px auto;
}