.footer {
    background-color: var(--primary);
    color: white;
    height: 100px;
    padding: 10px;
    box-sizing: border-box;
    width: calc(100% - 120px);
    margin-left: 120px;
    text-align: center;
}
.footer-links {
    list-style: none;
    display: flex;
    max-width: 300px;
    margin: auto;
    margin-bottom: 20px;
    justify-content: space-around;
}
.footer-link {
    text-decoration: none;
    text-transform: capitalize;
    color: white;
    font-family: cursive;
    font-weight: 400;
    transition: all 300ms;
}
.footer-link:hover {
    color: gray;
}

@media (max-width: 700px) {
    .footer {
        width: 100%;
        margin-left: 0px;
    }
}