.title {
    font-size: 1.5rem;
}

.dot-here-wrapper {
    position: absolute;
    display: block;
    padding: 0px;
    top: 0px;
    right: 0px;
    padding-top: 3px;
    box-sizing: border-box;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    cursor: pointer;
    transition: all 300ms;
}
.dot-here-wrapper:hover {
    box-shadow: 1px 1px 7px rgb(83, 83, 83);
    background-color: rgb(250, 250, 250);
}
.dot-here {
    display: block;
    border-radius: 50%;
    background-color: black;
    width: 3px;
    height: 3px;
    list-style-type: none;
    margin: 3px auto;
}
.edit-panel {
    position: absolute;
    z-index: +2;
    right: 0px;
    top: 10px;
    display: block;
    width: 80px;
    height: 60px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 1px 1px 7px rgb(83, 83, 83);
}
.edit-panel div {
    height: 50%;
    width: 100%;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    padding-top: 7px;
    box-sizing: border-box;
    font-size: 0.8rem;
    font-weight: 600;
    transition: all 700ms;
}
.edit-panel div:first-child {
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}
.edit-panel div:nth-child(2) {
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}
.edit-panel div:hover {
    background-color: rgb(175, 174, 174);
    cursor: pointer;
}
.prompt {
    position: absolute;
    width: 200px;
    height: 80px;
    padding: 10px;
    z-index: 3;
    color: white;
    font-weight: 600;
    background-color: var(--primary);
    border-radius: 3px;
    box-shadow: 1px 1px 7px white;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
}
.prompt button {
    margin-top: 9px;
    background-color: var(--green);
    color: white;
    font-weight: 600;
}
.prompt button:first-child {
    background-color: var(--red);
}