:root {
    --header-height: 75px;
}

.header {
    width: 100%;
    height: var(--header-height);
    background-color: var(--primary);
    top: 0%;
    left: 0px;
    margin-top: 0px;
    padding-top: 15px;
    position: fixed;
    box-sizing: border-box;
    z-index: 2;
}
.nav {
    display: flex;
    max-width: 100%;
    margin: auto;
    color: white;
    justify-content: space-between;
    list-style: none;
    flex-wrap: nowrap;
}
.logo {
    width: 90px;
}

.logo {
    width: 186px;
    font-size: 1.8rem;
    margin-top: -5px;
    margin-left: -25px;
}
.launch-search-panel {
    width: 25px;
    margin: 5px 15px;
    fill: white;
    cursor: pointer;
    display: none;
}